<template>
  <div class="containers">
    <div class="headers">
      <div class="headers-left">
        <img src="../assets/images/favicon.png">
      </div>
      <div class="headers-right">
        <p>{{ userName }}</p>
      </div>
    </div>
    <div>
      <div class="sides">
        <ul>
          <li @click="jumpUser" :class="{ hover: !mapView }">Employee</li>
          <li @click="jumpLocation" :class="{ hover: mapView }">Location</li>
          <li @click="logout">Log out</li>
        </ul>
      </div>
      <div class="mains">
        <MapView v-if="mapView" />
        <UserView v-else />
      </div>
    </div>
  </div>
</template>
<script setup>
import { computed, onMounted, onBeforeMount, ref } from 'vue'
import { useStore } from 'vuex'
import MapView from '../components/MapInfo.vue'
import UserView from '../components/UserInfo.vue'
import router from '@/router';
const store = useStore()
let mapView = ref(false)

const auth = computed(() => {
  return store.state.user.auth
})

const jumpLocation = () => {
  mapView.value = true
}

const userName = computed(() => {
  return store.state.user.userInfo
})

const jumpUser = () => {
  mapView.value = false
}

const logout = () => {
  localStorage.removeItem("token")
  localStorage.removeItem("userName")
  router.push("/")
}

onBeforeMount(() => {
  if (localStorage.getItem("token") !== null) {
    store.commit("auth", localStorage.getItem("token"))
    store.commit("userInfo", localStorage.getItem("userName"))
  } else {
    alert("access denied !")
  }
})

onMounted(() => {
  store.dispatch('employee', auth.value)
  store.dispatch("location", auth.value)
})
</script>
