<template>
    <div class="container-fluid text-center signupForm">
        <form class="col-4 p-5 rounded-3 animate__animated animate__fadeInRight" onsubmit="return false">
            <h1>Sign Up</h1>
            <div class="mb-3">
                <label for="accountName" class="form-label">Account Name:</label>
                <input v-model="userName" type="text" class="form-control" id="accountName" aria-describedby="emailHelp">
            </div>
            <div class="mb-3">
                <label for="password" class="form-label">Password:</label>
                <input v-model="password" type="password" class="form-control" id="password" aria-describedby="emailHelp">
            </div>
            <div class="mb-3">
                <label for="replayPassword" class="form-label">Replay Password:</label>
                <input v-model="passwordReplay" type="password" class="form-control" id="replayPassword">
            </div>
            <div class="alert alert-danger  tipBox" role="alert" v-if="error !== null">
                {{ error }}
            </div>
            <div class="mb-3" id="button">
                <button class="btn btn-primary" @click="toSignup">Sign up</button>
                <button class="btn btn-secondary" @click="toLogin">Log in</button>
            </div>
        </form>
    </div>
</template>
<script setup>
import { ref, computed, onMounted } from 'vue'
import { useRouter } from 'vue-router'
import { useStore } from 'vuex'
const store = useStore()
const router = useRouter()

let userName = ref()
let password = ref()
let passwordReplay = ref()

let error = computed(() => {
    return store.state.errorMessage
})

let isEmpty = (str) => {
    if (str == null || str.length == 0) {
        return true
    } else {
        return false
    }
}

let toSignup = () => {
    if (!isEmpty(userName.value)) {
        if (!isEmpty(password.value) && !isEmpty(passwordReplay.value)) {
            if (password.value === passwordReplay.value) {
                if (password.value.length > 6) {
                    let checkReg = new RegExp('(?=.*[0-9])(?=.*[A-Z])(?=.*[a-z])')
                    if (checkReg.exec(password.value)) {
                        let user = {
                            username: userName.value,
                            password: password.value
                        }
                        store.dispatch("signup", { user, router })
                    } else store.commit('error', "The password must be at least six characters long and contain both uppercase letters, lowercase letters, and numbers")
                } else store.commit('error', "The password must be at least six characters long")
            } else store.commit('error', "The passwords are inconsistent")
        } else store.commit('error', "Password cannot be empty")
    } else store.commit('error', "The user name cannot be empty")
}

onMounted(() => {
    store.commit('error', null)
})

let toLogin = () => {
    router.push('/login')
}

</script>
