<template>
    <div class="Theme" :class="theme ? 'Theme-sun' : 'Theme-night'">
        <div class="containers">
            <div class="container-background animate__animated animate__fadeInRight" @click="theme = !theme">
                <div class="container-background-sun-off hidden"></div>
                <div class="container-background-sun-on"></div>
            </div>
            <div class="container-banner animate__animated animate__fadeIn">
                <img :src="reloadGif">
            </div>
            <div class="container-main">
                <h1 class="container-title animate__animated animate__pulse">Employees Manager</h1>
                <hr>
                <p class="container-description animate__animated animate__slideInUp">This is a <span
                        class="pc-version">employees</span> manager</p>
                <div class="container-main-button animate__animated animate__slideInUp">
                    <button class="animate__animated animate__slideInUp container-main-button-login btn btn-primary"
                        type="button" @click="toLogin">Log
                        in</button>
                    <button class="animate__animated animate__slideInUp container-main-button-signup btn btn-primary"
                        type="button" @click="toSignup">Sign
                        up</button>
                </div>
            </div>
        </div>
    </div>
</template>
<script setup>
import { ref, computed, onMounted } from 'vue'
import october from '../assets/images/hello-october.gif'
import { useRouter } from 'vue-router'

const router = useRouter()
let theme = ref()

let reloadGif = computed(() => {
    return october + '?+' + new Date()
})

onMounted(() => {
    let currdate = new Date();
    if (currdate.getHours() >= 23 || currdate.getHours() < 7) {
        theme.value = false;
    } else {
        theme.value = true;
    }
})


let toLogin = () => {
    router.push('/login')
}

let toSignup = () => {
    router.push('/signup')
}
</script>
